html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.enhanced {
  background-color: gold;
  border-radius: 5px;
  padding: 13px 18px;
}

